.post-content img {
  max-width: 100%;
  width: 300px !important;
  height: 300px !important;
  object-fit: cover !important;
  display: block;
  margin-bottom: 1rem;
  border-radius: 12px;
  border: 2px solid #ccc;
}

.App {
  display: flex;
  justify-content: center;
  padding: 2rem;
}

.App > div {
  max-width: 800px;
  width: 100%;
}

body {
  margin: 0;
  background-color: #f6f6f6; /* light grey background outside content */
  font-family: sans-serif;
}

.App {
  display: flex;
  justify-content: center;
  padding: 2rem;
}

.content-wrapper {
  background-color: #fff;     /* white content box */
  padding: 2rem;
  max-width: 800px;
  width: 100%;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1); /* optional soft shadow */
  border-radius: 8px;
}

.post-wrapper {
  margin-bottom: 2rem; /* space between posts */
  padding-bottom: 1rem;
  border-bottom: 1px solid #f1f1f1; /* optional: adds subtle divider */
}

@media (max-width: 768px) {
  body {
    background-color: white; /* âœ… remove gray background on mobile */
  }

  .content-wrapper {
    background-color: transparent; /* remove white card */
    padding: 0;                 /* less padding on small screens */
    box-shadow: none;              /* remove shadow */
    border-radius: 0;              /* remove rounded corners */
  }

}





